<template>
	<transition name="todo" appear>
		<li>
			<label>
				<input type="checkbox" :checked="todo.done" @change="changeStatus(todo.id)" />
				<span v-show="!todo.isEdit">{{todo.title}}</span>
				<input class="rename" type="text" :value="todo.title" v-show="todo.isEdit" @blur="update(todo, $event)"
					@keyup.enter="update(todo, $event)" ref="rename">
			</label>
			<button class="btn btn-danger" @click="delT(todo.id)">删除</button>
			<button class="btn btn-edit" @click="show(todo)" v-show="!todo.isEdit">编辑</button>
		</li>
	</transition>
</template>

<script>
	export default {
		name: 'MyItem',
		props: ['todo'],
		methods: {
			changeStatus(id) {
				this.$bus.$emit('check', id);
			},
			delT(id) {
				if (confirm('确认删除吗？')) this.$bus.$emit('delItem', id);
			},
			show(todo) {
				todo.isEdit = true;
				// setTimeout(()=>this.$refs.rename.focus(), 100);
				this.$nextTick(() => this.$refs.rename.focus())
				// console.log(this.$refs.rename);
			},
			update(todo, event) {
				todo.isEdit = false;
				// console.log(event.target.value);
				if (event.target.value.trim()) this.$bus.$emit('update', todo.id, event.target.value);
			}
		}
	}
</script>

<style scoped>
	li {
		list-style: none;
		height: 36px;
		line-height: 36px;
		padding: 0 5px;
		border-bottom: 1px solid #ddd;
	}

	li label {
		float: left;
		cursor: pointer;
	}

	li label li input {
		vertical-align: middle;
		margin-right: 6px;
		position: relative;
		top: -1px;
	}

	li button {
		float: right;
		display: none;
		margin-top: 3px;
	}

	li:before {
		content: initial;
	}

	li:last-child {
		border-bottom: none;
	}

	li:hover {
		background: #ddd;
	}

	li:hover button {
		display: block;
	}

	span {
		padding-left: 15px;
	}

	.rename {
		width: 360px;
		outline: none;
		padding: 5px 15px;
		font-size: 16px;
		border: #00A8A8 solid 1px;
		box-shadow: 0px 0px 10px #8fcad5;
	}
	
	.todo-enter-active {
		animation: todo .4s ease-in-out;
	}
	
	.todo-leave-active {
		animation: todo .4s ease-in-out reverse;
	}
	
	@keyframes todo {
		from {
			transform: translateX(100%);
		}
	
		to {
			transform: translateX(0);
		}
	}
</style>
