<template>
	<div id="root">
		<transition name="coming" appear>
			<div class="todo-container" v-show="bool">
				<div class="todo-wrap">
					<MyHeader @addTodo="addTodo"></MyHeader>
					<MyList :todos="todos"></MyList>
					<MyFooter :todos="todos" :checkAll="checkAll" :removeAll="removeAll"></MyFooter>
				</div>
			</div>
		</transition>
		<button type="button" @click="bool = !bool" id="magic">动画展示</button>
	</div>
</template>

<script>
	import MyHeader from './components/MyHeader.vue';
	import MyList from './components/MyList.vue';
	import MyFooter from './components/MyFooter.vue';
	export default {
		name: 'App',
		components: {
			MyHeader,
			MyList,
			MyFooter
		},
		data() {
			return {
				todos: JSON.parse(localStorage.getItem('todos')) || [],
				bool: true,
			}
		},
		methods: {
			addTodo(todObj) {
				this.todos.unshift(todObj);
			},
			check(id) {
				this.todos.forEach((todo) => {
					if (todo.id === id) todo.done = !todo.done;
				})
			},
			delItem(id) {
				this.todos = this.todos.filter(todo => todo.id !== id);
			},
			checkAll() {
				if (this.todos.every((todo) => todo.done === true)) {
					this.todos.forEach((todo) => todo.done = false);
				} else {
					this.todos.forEach((todo) => todo.done = true);
				}
			},
			removeAll() {
				if (confirm('确定清除所有已完成任务吗？')) {
					this.todos = this.todos.filter(todo => todo.done !== true);
				}
			},
			update(id, newTitle) {
				this.todos.forEach((todo) => {
					if (todo.id === id) todo.title = newTitle;
				})
			}
		},
		watch: {
			todos: {
				deep: true,
				handler(value) {
					// console.log(JSON.stringify(value));
					localStorage.setItem('todos', JSON.stringify(value))
				}
			}
		},
		mounted() {
			this.$bus.$on('check', this.check);
			this.$bus.$on('delItem', this.delItem);
			this.$bus.$on('update', this.update);
		},
		beforeDestroy() {
			this.$bus.$off('check');
			this.$bus.$off('delItem');
			this.$bus.$off('update');
		}
	}
</script>

<style>
	body {
		background: #fff;
	}

	.btn {
		display: inline-block;
		padding: 4px 12px;
		margin-bottom: 0;
		font-size: 14px;
		line-height: 20px;
		text-align: center;
		vertical-align: middle;
		cursor: pointer;
		box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
		border-radius: 4px;
	}

	.btn-danger {
		color: #fff;
		background-color: #da4f49;
		border: 1px solid #bd362f;
	}

	.btn-danger:hover {
		color: #fff;
		background-color: #bd362f;
	}

	.btn-edit {
		color: #fff;
		background-color: #00a8a8;
		border: 1px solid #3799bd;
		margin-right: 10px;
	}

	.btn-edit:hover {
		color: #fff;
		background-color: #3a7696;
	}

	.btn:focus {
		outline: none;
	}

	.todo-container {
		width: 600px;
		margin: 0 auto;
	}

	.todo-container .todo-wrap {
		padding: 10px;
		border: 1px solid #ddd;
		border-radius: 5px;
	}
	
	#magic {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
	}

	.coming-enter-active {
		animation: coming .4s ease-in-out;
	}

	.coming-leave-active {
		animation: coming .4s ease-in-out reverse;
	}

	@keyframes coming {
		from {
			transform: translateY(-100%);
		}

		to {
			transform: translateY(0);
		}
	}
</style>
