<template>
	<div class="todo-footer" v-show="todos.length">
		<label>
			<input type="checkbox" :checked="finished === todos.length" @click="checkAll"/>
		</label>
		<span>
			<span>已完成{{finished}}</span> / 全部{{todos.length}}
		</span>
		<button class="btn btn-danger" @click="removeAll">清除已完成任务</button>
	</div>
</template>

<script>
	export default {
		name: 'MyFooter',
		props: ['todos', 'checkAll', 'removeAll'],
		computed: {
			finished() {
				// let a = 0;
				// this.todos.forEach((todo)=>{
				// 	if (todo.done) a++;
				// });
				// return a
				return this.todos.reduce((pre, current)=>{
					return pre + (current.done)
				}, 0)
			}
		}
	}
</script>

<style scoped>
	.todo-footer {
		height: 40px;
		line-height: 40px;
		padding-left: 6px;
		margin-top: 5px;
	}

	.todo-footer label {
		display: inline-block;
		margin-right: 20px;
		cursor: pointer;
	}

	.todo-footer label input {
		position: relative;
		top: -1px;
		vertical-align: middle;
		margin-right: 5px;
	}

	.todo-footer button {
		float: right;
		margin-top: 5px;
	}
	
	span {
		user-select: none;
	}
</style>
